  background: rgba(50, 50, 50, 0.4);
}
/* Modifier: `uk-dotnav-contrast`
 ========================================================================== */
.uk-dotnav-contrast > * > * {
  background: rgba(255, 255, 255, 0.4);
}
/*
 * Hover
 * 1. Apply hover style also to focus state
