  }
  body {
    overflow-y: visible !important;
    background: #fff;
    padding: 20px !important;
  }
  body:after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    display: block;
    z-index: 9999;
  }
  #sidebar_main {
    left: -240px !important;
  }
  .main-print {
    padding: 0 !important;
    margin: 0 !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
  }
  .md-card-single {
    margin: 0 !important;
  }
  #page_content {
    margin: 0 !important;
    overflow-y: visible !important;
    height: auto !important;
    transition: none !important;
  }
  #page_content:before {
    display: none !important;
  }
  .print_bg,
  .print_bg > * {
    -webkit-print-color-adjust: exact;
  }
  .reset-print {
    left: 0 !important;
    right: 0 !important;
    margin: 0 !important;
    padding: 0 !important;
    width: 100% !important;
  }
  .md-card-content {
    height: auto !important;
  }
